import {
  CardName,
  CardText,
  Grid,
  GridItem,
  StyledImg,
} from 'components/App/App.styled';
import { Button } from 'components/Button/Button';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useLocation } from 'react-router-dom';

import { getfilteredByName } from 'redux/operations';
import { getCount, getFilteredCharacters, getName } from 'redux/selectors';

const CharactersList = () => {
  const [page, setPage] = useState(0);
  let count = useSelector(getCount) > 20;
  const location = useLocation();
  const dispatch = useDispatch();
  const characters = useSelector(getFilteredCharacters) ?? [];
  const name = useSelector(getName);

  useEffect(() => {
    setPage(2);
  }, [name]);
  const baseImg =
    'data:image/png;base64,';
  return (
    <>
      <Grid>
        {[...characters]
          .sort((a, b) => a.name.localeCompare(b.name))
          .map(({ id, image, name, species }) => (
            <GridItem key={id}>
              <Link
                to={`/character/${decodeURIComponent(
                  name
                    .toLowerCase()
                    .trim()
                    .split(' ')
                    .map((word, index) => {
                      if (index === 0) {
                        return word;
                      }
                      return word.charAt(0).toUpperCase() + word.slice(1);
                    })
                    .join('')
                )}/${id}/`}
                state={{ from: location }}
              >
                <StyledImg src={image ? image : baseImg} alt={name} />
              </Link>
              <CardName>{name}</CardName>
              <CardText>{species}</CardText>
            </GridItem>
          ))}
      </Grid>
      {count && (
        <Button
          onClick={() => {
            setPage(Number(page) + 1);
            console.log(page, name);
            dispatch(getfilteredByName({ name, page }));
            window.scrollTo(0, 0);
          }}
        />
      )}
    </>
  );
};

export default CharactersList;
